import { mapMutations } from 'vuex'

export default {
  name: 'discovery-min-tab',
  mixins: [],
  data () {
    return {}
  },
  computed: {},
  components: {
  },
  watch: {},
  methods: {
    selectTrend (item) {
      this.setDiscoveryItem(item)
    },
    ...mapMutations({
      'setDiscoveryItem': 'SET_DISCOVERY_ITEM'
    })
  },
  created () {},
  mounted () {}
}



